.socials .ico-circle {
    height: 40px;
    width: 40px;
    font-size: 1.7rem;
    border-radius: 50%;
    line-height: 1.4;
    margin: 0 15px 10px 0;
    color: #ff533d;
    box-shadow: 0 0 0 3px #ff533d;
    transition: all 500ms ease;
    display: inline-block;
    text-align: center;
}

.socials .ico-circle:hover {
    background-color: #ff533d;
    color: #f5f5f5;
    box-shadow: 0 0 0 3px #ff998c;
    transition: all 500ms ease;
  }